.LoadingFullScreen {
  width: 100vw;
  height: 100vh;
}

.LoadingIcon {
  font-size: 64px;
  margin: 20px auto;
  width: 66%;
  -webkit-animation-name: drive;
          animation-name: drive;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes drive {
  0% {
    text-align: right;
  }

  50% {
    text-align: center;
  }

  100% {
    text-align: left;
  }
}

@keyframes drive {
  0% {
    text-align: right;
  }

  50% {
    text-align: center;
  }

  100% {
    text-align: left;
  }
}




